
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import qs from 'qs'
export default {
components: {
XDialog,
Toast,
Group,
XSwitch,
XButton
},
data () {
return {
acount: '',
dialog: true,
password: '',
repassword: '',
position: 'default',
error: false,
message: '',
disabled: false,
clock: ''
}
},
methods: {
showError (msg) {
this.message = msg
this.error = true
},
sendRegister () {
this.$http.post('/api/user/created', qs.stringify({
Acount: this.acount,
Password1: this.password,
Password2: this.repassword
})).then((response) => {
if (response.data.status === 10000) {
this.showError('注册成功')
this.$router.push('/login')
} else if (response.data.status === 10001) {
this.showError('该账号已被注册')
} else if (response.data.status === 10002) {
this.showError('账号不能为空！')
} else if (response.data.status === 10003) {
this.showError('密码不得少于六位！')
} else if (response.data.status === 10004) {
this.showError('两次输入的密码不一致！')
} else {
this.showError('系统错误，请重试')
}
})
}
},
created () {
}
}



