  export default{
    created () {
//      this.$vux.loading.show({
//        text: '加载中'
//      })
//      this.listRe()
      console.log(this.$root.Bus)
    },
    data () {
      return {
        recommend: [
          {
            Id: 1,
            Cover: '/static/images/home-recommend1@2x.png',
            Title: 'MISIS-XIAOHUIWANG跨年',
            Summary: '跨界·珠宝 合作展',
            UpdatedTime: '2017/07/11'
          },
          {
            Id: 2,
            Cover: '/static/images/home-recommend2@2x.png',
            Title: '黄原书画奖学金作品会',
            Summary: '其宗旨是鼓励学生在探索中国书画艺术的道路中',
            UpdatedTime: '2017/08/31'
          }
        ]
      }
    },
    methods: {
      listRe () {
        this.$http.get('/api/tourism/list?p=1&pers=3').then((response) => {
          this.$vux.loading.hide()
          if (response.data.status === 10000) {
            this.recommend = response.data.data
          }
        })
      },
      toTravl () {
        this.$router.push({
          path: '/travel'
        })
      }
    },
    watch: {
      $route () {
        document.documentElement.scrollTop = document.body.scrollTop = 0
      }
    },
    filters: {
      onlyData: function (value) {
        return value.substring(0, 10)
      },
      describeOver: function (value) {
        if (value[0].length > value[1]) {
          return value[0].substring(0, value[1]) + '...'
        } else {
          return value[0]
        }
      }
    }
  }



