import qs from 'qs'
export default {
  data () {
    return {
      topath: '',
      isHome: 1,
      mobile: '',
      postpath: ''
    }
  },
  name: 'app',
  created () {
    this.nowPosition(this.$route.path)
  },
  watch: {},
  methods: {
    noOpen () {
      this.$vux.alert.show({
        title: '提示',
        content: '暂未开放，敬请期待',
        onShow () {
        },
        onHide () {
        }
      })
    },
    nowPosition (path) {
      if (path.indexOf('product') !== -1 || path.indexOf('travel') !== -1 || path.indexOf('confirmOrder') !== -1) {
        this.isHome = 4
      } else if (path.indexOf('home') !== -1 || path.indexOf('searchResult') !== -1) {
        this.isHome = 1
      } else if (path.indexOf('forum') !== -1 || path.indexOf('forumdetail') !== -1 || path.indexOf('community') !== -1) {
        this.isHome = 2
      } else if (path.indexOf('informations') !== -1 || path.indexOf('infodetail') !== -1) {
        this.isHome = 3
      } else if (path.indexOf('personal') !== -1 || path.indexOf('main') !== -1 || path.indexOf('user') !== -1 || path.indexOf('registration') !== -1 || path.indexOf('appraise') !== -1 || path.indexOf('wechatRegister') !== -1) {
        this.isHome = 5
      }
    }
  }
}



