
import XDialog from 'vux/src/components/x-dialog/index.vue'
import Toast from 'vux/src/components/toast/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XSwitch from 'vux/src/components/x-switch/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

import qs from 'qs'
export default {
components: {
XDialog,
Toast,
Group,
XSwitch,
XButton
},
data () {
return {
position: 'default',
captcha_id: 0,
captcha_src: '',
img_code: '',
acount: '',
password: '',
error: false,
message: ''
}
},
methods: {










isloaded () {
this.$http.get('/api/user/isLogin').then((response) => {
this.$vux.loading.hide()
if (response.data.status === 10000) {
this.$router.push('/personal')
}
})
},
showError (msg) {
this.message = msg
this.error = true
},
sendLogin () {
this.$http.post('/api/user/login', qs.stringify({
Account: this.acount,
Password: this.password
})).then((response) => {
if (response.data.status === 10000) {
this.$router.push('/personal')
} else if (response.data.status === 10001) {
this.showError('该用户不存在')
} else if (response.data.status === 10002) {
this.showError('密码错误')
} else {
this.showError('系统错误，请重试')
}
})
}
},
mounted () {

},
created () {
this.$vux.loading.show({
text: '加载中'
})
this.isloaded()
}
}



